type OperatorKey = '^' | '*' | '/' | '+' | '-';

const operators: {
  [key in OperatorKey]: { precedence: number, associativity: 'Left' | 'Right' }
} = {
  '^': { precedence: 4, associativity: 'Right' },
  '*': { precedence: 3, associativity: 'Left' },
  '/': { precedence: 3, associativity: 'Left' },
  '+': { precedence: 2, associativity: 'Left' },
  '-': { precedence: 2, associativity: 'Left' },
};

export { operators }
export { type OperatorKey }